.friendList {
  margin: 30px auto 0;
  padding: 0;
  width: 300px;
  list-style: none;
}
.item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 3px 4px 5px 0px rgba(0, 0, 0, 0.38);
  margin: 15px 0 15px;
  border-radius: 3%;
  height: 95px;
  max-width: 200px;
  transition: all 200ms linear;
  cursor: pointer;
}
.item:hover {
  box-shadow: 4px 4px 7px 5px rgba(0, 0, 0, 0.38);
}
.status {
  display: block;
  height: 20px;
  width: 20px;
  background-color: rgb(235, 25, 25);
  border-radius: 50%;
  margin: 0 8px 0;
}
.isActive {
  background-color: rgb(17, 168, 11);
}
.avatar {
  border-radius: 20%;
}
.name {
  font-family: Arial, serif;
  font-size: 18px;
  font-weight: 500;
  margin: 0 10px 0;
}
