.statistics {
  margin: 20px auto 0;
  width: 500px;
  height: 200px;
  background-color: #fff;
  text-align: center;
  font-family: Arial, serif;
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.51);
}
.title {
  height: 50%;
  background-color: rgb(221, 244, 248);
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.statList {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  grid-template-rows: 100px;
}
.item {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  transition: all 200ms linear;
  cursor: pointer;
  border: rgba(225, 228, 233, 0.8) solid 1px;
  border-radius: 1%;
}
.item:hover {
  opacity: 0.7;
}
.label {
  margin-top: 20px;
  font-size: 16px;
  width: 100%;
}
.percentage {
  font-size: 22px;
}
